<template>
    <div>
        <!-- <modal-address-content :addresses="addresses" v-if="daftarAlamatActive"></modal-address-content> -->
        <tambah-alamat v-if="tambahAlamatActive"></tambah-alamat>
        <template v-else>
            <h4 class="box text-center">Ulun Antarkan ke -</h4>
            <div class="box">
                <!-- <div class="box-child" @click="setModalAddress"> -->
                <div class="box-child" @click="setTambahAlamat">
                    <div class="box-content">
                        <span class="text-head">Alamat Pengiriman</span>
                        <span class="text-child" v-if="addresses.length>0">{{addresses[0].alamat}}</span>
                        <span class="text-child text-warning" v-else>Belum mengatur alamat pengiriman</span>
                    </div>
                    <i class="fa fa-angle-right" aria-hidden="true"></i>
                </div>
            </div>
            <div class="box">
                <div class="box-child">
                    <div class="box-content">
                        <span class="text-head">Nomor Whatsapp</span>
                        <span class="text-child">085746080544</span>
                    </div>
                    <i class="fa fa-angle-right" aria-hidden="true"></i>
                </div>
            </div>
            <div class="box">
                <div class="box-child">
                    <div class="box-content w-100">
                        <span class="text-head">Metode Pembayaran</span>
                        <div class="container-payments w-100 text-child">
                            <div class="form-check py-2" v-for="data in payments" :key="data.id" @click="payment = data.id">
                                <input :value="data.id" :checked="data.id==payment" class="form-check-input" type="radio" name="payment">
                                <img style="height: 25px" :src="$store.state.url_images+'payment/'+data.image">
                            </div>
                        </div>
                    </div>
                </div>
            </div>
            <div class="">
                <span class="font-weight-bold">Rute Pengantaran</span>
                <GoogleMapLoader style="height:350px;" class="mb-3" v-if="ongkir!=null&&product!=null" @setLoading="p=>loading=p" @setOngkir="p => ongkir=p" :mapConfig="mapConfig" :destinations="get_start_end_waypoint"/>	
            </div>
            <div class="box" style="padding-bottom: 100px">
                <div class="box-child">
                    <div class="box-content">
                        <span class="font-weight-bold">Rincian Harga</span>
                        <table class="">
                            <tr>
                                <td>Masuk Tabungan</td>
                                <td v-if="ongkir!=null&&!paymentIsTransfer" class="pl-2">Rp {{toCurrency(pembulatan(total)-total)}},-</td>
                                <td v-else class="pl-2">-</td>
                            </tr>
                            <tr>
                                <td>Ongkos Kirim</td>
                                <td v-if="ongkir!=null" class="pl-2">Rp {{toCurrency(ongkir)}},-</td>
                                <td v-else class="pl-2">-</td>
                            </tr>
                        </table>
                    </div>
                </div>
            </div>
            <div class="fixed-bottom bg-white container-footer border-top">
                <div class="d-flex justify-content-between py-2 mx-2">
                    <div class="d-flex flex-column">
                        <span class="text-muted small">TOTAL PESANAN</span>
                        <span class="font-weight-bold">
                            <i v-if="loading" class="fa fa-spinner fa-spin"></i>
                            <template v-else>Rp {{toCurrency(paymentIsTransfer ? total : pembulatan(total))}}</template>
                        </span>
                        <span class="text-muted small">Total yang Pian bayarkan</span>
                    </div>
                    <div class="d-flex align-items-center">
                        <button v-if="loading" class="btn btn-lg btn-block text-white rounded-0 px-5 tombol">
                            <i class="fa fa-spinner fa-spin"></i>
                        </button>
                        <button @click="checkout" v-else class="btn btn-lg btn-block text-white rounded-0 px-5 tombol">Tukar</button>
                    </div>
                </div>
            </div> 
        </template>   
    </div>
</template>
<script>
import GoogleMapLoader from '@/components/Map/GoogleMapLoader';
// import ModalAddressContent from './ModalAddress/ModalAddressContent.vue';
import TambahAlamat from './ModalAddress/TambahAlamat.vue';
export default {
    name:"AddressPage",
    components:{
        GoogleMapLoader,
        // ModalAddressContent,
        TambahAlamat
    },
    props:{
        addresses:Array,
    },
    data(){
        return {
            loading:false,
            mapConfig:{
                zoom: 15,
                center: {
                    lng: 116.23,
                    lat: -3.23,
                },
                gestureHandling: "greedy",
                mapTypeId: 'hybrid',
                disableDefaultUI: true,
                zoomControl: true,
            },
            ongkir:null,
            product:null,
            payment:1,
            // data_user:null
        }
    },
    computed:{
        get_start_end_waypoint: function () {
            var result = Array.from(new Set(this.product.map(s => s.product.penjual_model.id))).map(id => {
                return {
                    lat: this.product.find(s => s.product.penjual_model.id === id).product.penjual_model.lat,
                    lng: this.product.find(s => s.product.penjual_model.id === id).product.penjual_model.lng,
                }
            })
            var end = {
                lat: this.address.lat,
                lng: this.address.lng
            };
            var waypoints=[];
            var start = result[0];
            if (result.length > 1) {
                waypoints = result.splice(1);
            }
            var waypts = [];
            for (var i = 0; i < waypoints.length; i++) {
                waypts.push({
                    location: waypoints,
                    stopover: true
                });
            }
            return {
                origin: start,
                destination: end,
                waypoints: waypts
            };
        },
        tambahAlamatActive(){
            const urlParams = new URLSearchParams(window.location.search)
            var u = JSON.parse(urlParams.get('modals'));
            if(Array.isArray(u)){
                return u.includes('tambah-alamat')
            }else{
                return false;
            }
        },
        address(){
            return this.addresses.find(p => p.alamat_utama)
        },
        payments(){
            return this.$store.state.transaction.payments;
        },
        total(){
            var total;
            if(this.product!=null){
                total = this.product.reduce((accumulator, current) => accumulator + current.total_price, 0)
            }else{
                total =  '-'
            }
            if(this.ongkir!=null&&total!='-'){
                total+=this.ongkir
            }
            return total;
        },
        paymentIsTransfer(){
            if(this.payments!=null){
                if(this.payments.length>0){
                    return this.payments.find(p => p.id==this.payment).transfer
                }else{
                    return null
                }
            }else{
                return null
            }
        }
    },
    async created(){
        // this.account();
        this.loading = true;
        if(this.$store.state.login.ifLogin){
            var items = typeof(this.$route.query.items) == "object" ? this.$route.query.items.join(',') : this.$route.query.items;
            await this.axios.get(this.$store.state.url_api + "cart?transaction_number="+items, {
                headers: {
                    "Authorization": "Bearer " + localStorage.token
                },
            }).then(response => {
                if(response.data.length>0){
                    this.product = response.data.map(el => {
                        el.timeout = null;
                        el.cancelToken = null;
                        el.loadingDelete = false;
                        el.checked=false;
                        return el;
                    });
                }
            })
            if(this.addresses.length>0&&this.$route.query.items!=undefined){
                await this.axios.get(this.$store.state.url_api + "ongkir?transaction_number="+this.$route.query.items, {
                    headers: {
                        "Accept": "Application/json",
                        "Authorization": "Bearer " + localStorage.token
                    },
                }).then(response => {
                    if(response.status==200){
                        this.ongkir = response.data.data.ongkir;
                    }
                })
            }

            await this.axios.get(this.$store.state.url_api + "payments", {
                headers: {
                    "Authorization": "Bearer " + localStorage.token
                },
            }).then(response => {
                this.$store.dispatch('save_payments', response.data);
                this.loading = false;
            })
        }
        if(this.addresses.length<1){
            this.loading = false;
        }
    },
    methods:{
        // account(){
        //     this.axios.get(this.$store.state.url_api + "account").then(response => {
        //         this.data_user=response.data;
        //     })
        // },
        toCurrency(angka) {
            angka += '';
			// angka = angka.replace(".",",");
			var x = angka.split('.');
			var x1 = x[0];
			var x2 = x.length > 1 ? '.' + x[1] : '';
			var rgx = /(\d+)(\d{3})/;
			while (rgx.test(x1)) {
				x1 = x1.replace(rgx, '$1' + '.' + '$2');
			}
			return x1 + x2;
        },
        // setModalAddress(){
        //     var queryParams = new URLSearchParams(window.location.search);
        //     var q = ['daftar-alamat'];
        //     queryParams.set("modals",JSON.stringify(q));
        //     this.$router.push(this.$route.path+'?'+queryParams.toString())    
        // }
        setTambahAlamat(){
            var queryParams = new URLSearchParams(window.location.search);
            var q = ['tambah-alamat'];
            queryParams.set("modals",JSON.stringify(q));
            var payload = this.$store.state.transaction.addresses[0]
            this.$store.dispatch('address/save_address', {
                    desa:payload.desa,
                    rt:payload.rt,
                    rw:payload.rw,
                    kode_pos:payload.kode_pos,
                    details:payload.jalan,
            })
            this.$router.push(this.$route.path+'?'+queryParams.toString())    
        },

        setPayment(id){
            this.payment = id;
            
        },

        pembulatan(total){
            return Math.ceil(total / 1000) * 1000;
        },
        checkout(){
            var payload = {
                transaction_number:this.$route.query.items,
                payment_method:this.payment
            }
            this.axios.post(this.$store.state.url_api + "transaction/checkout", payload, {
                headers : {
                    "Accept": "Application/json",
                    "Authorization"   : "Bearer " + localStorage.token
                }
            }).then(response => {
                if(response.status === 201){
                    this.$router.replace({name:'checkout', params:{id:response.data.data.checkout_number}})
                }else if(response.status === 400){
                    console.log(response.data.meta.message);
                }else{
                    console.log(response.data.meta.message);
                }
            })
        }
    }
}
</script>

<style scoped>
    .container-payments {
        display: grid;
        grid-template-rows: repeat(4, 1fr);
        grid-auto-flow: column;
        overflow-x:auto;
        grid-auto-columns: min-content;
        grid-column-gap: 30px;
    }
    .container-payments > *{
        width:auto;
    }
</style>