<template>
    <div class="h-100">
        <div class="google-map" ref="googleMap"></div>
        <!-- <button @click="setKordinat" class="btn btn-lg text-white mt-3 rounded-0 px-5 tombol float-right">Set Lokasi</button> -->
        <modal-warning :ids="'location_not_valid'" :message="{title:'Lokasi yang anda masukkan tidak valid',deskripsi:''}"></modal-warning>
    </div>
</template>
<script>
    import GoogleMapsApiLoader from 'google-maps-api-loader'
    import modalWarning from '@/components/modal/Failed'
    import $ from 'jquery'
    export default {
        props: {
            mapConfig: Object,
            isSet:Boolean
        },
        components:{
            modalWarning
        },
        data() {
            return {
                google: null,
                map: null,
                center:null,
            }
        },
        computed:{
            near_location(){
                return this.$store.state.location.near_location
            }
        },
        watch:{
            near_location(){
                console.log('dsd')
                this.setCenter(this.near_location)
            }
        },
        async created() {
            const googleMapApi = await GoogleMapsApiLoader({
                apiKey: "AIzaSyDFpkPVjM26Py4C4pDe9RiCVWPl4s_pXrw"
            })
            this.google = googleMapApi
            await this.initializeMap()
            if(this.$store.state.location.near_location!=null){
                this.setCenter(this.near_location)
            }else{
                this.$router.push({
                    name:'address',
                    query:{
                        items:this.$route.query.items
                    }
                });
            }
        },

        methods: {
            initializeMap() {
                const mapContainer = this.$refs.googleMap
                this.map = new this.google.maps.Map(mapContainer, this.mapConfig)
                // if(this.isSet){
                    this.addCenterMarker()
                // }else{
                //     this.addOverlay();
                // }
                this.map.addListener("center_changed", () => {
                    // console.log(this.map.getCenter().toJSON())
                });
            },

            addCenterMarker(){
                const centerIcon = document.createElement("div");
                centerIcon.className = 'centerMarker';
                centerIcon.style.position = 'absolute';
                centerIcon.style.top = '50%'
                centerIcon.style.left = '50%'
                centerIcon.style['z-index'] = '30'
                centerIcon.style.margin = '-34px 0 0 -10px'
                centerIcon.style.height = '34px'
                centerIcon.style.width = '20px'
                centerIcon.style.cursor = 'pointer'
                const image = document.createElement('img');
                image.src = require('@/assets/marker.png');
                image.style.width = "100%";
                centerIcon.appendChild(image);
                this.$refs.googleMap.appendChild(centerIcon);
                const button = document.createElement("button");
                button.className = 'btn text-white';
                button.style.position = 'absolute';
                button.style.top = '35%'
                button.style.left = '50%'
                button.style['z-index'] = '30'
                // button.style.border = '3px solid #ffa74c';
                button.style.backgroundColor = '#ffa74c';
                button.style.borderRadius = '5px';
                button.style.marginLeft="-54px";
                button.style.cursor = 'pointer'
                button.innerText="Set Kordinat";
                button.setAttribute('ref','setKordinat')
                var that =  this;
                button.onclick= function(){
                    that.setKordinat()
                }
                this.$refs.googleMap.appendChild(button);
            },

            addOverlay(){
                const overlay = document.createElement("div");
                overlay.className = 'overlay';
                overlay.style.position = 'absolute';
                overlay.style.backgroundColor = "#000";
                overlay.style.top = '0'
                overlay.style.height = '100%'
                overlay.style.width = '100%'
                overlay.style.opacity = '0.6'
                this.$refs.googleMap.appendChild(overlay);
                this.map.setOptions({draggable:false})
            },

            setCenter(center){
                if(this.map!=null){
                    this.map.setOptions({center:{
                        lat:center.lat,
                        lng:center.lng
                    }})
                }
            },
            setKordinat(){
                var directionsService = new this.google.maps.DirectionsService;
                directionsService.route({
                    origin:{lng: 116.23, lat: -3.23},
                    destination: {lat:this.map.getCenter().lat(), lng:this.map.getCenter().lng()},
                    optimizeWaypoints: true,
                    travelMode: 'DRIVING',
                }, (response, status) => {
                    if (status === 'OK') {
                        this.$store.dispatch('address/save_kordinat',{
                            lat:this.map.getCenter().lat(),
                            lng:this.map.getCenter().lng()
                        })
                        this.$router.back();
                    } else {
                        $('#location_not_valid').modal('show');
                    }
                });
            }
        }
    }
</script>
<style scoped>
    .google-map {
        width: 100%;
        /* height: 350px; */
        height:100%;
    }
    .overlay{
        position: absolute;
        top: 0px;
        width: 100%;
        height: 100%;
        background-color: #000;
        opacity: 0.5;
    }

    .opaque-text{
        position: absolute;
        display: flex;
        justify-content: space-evenly;
        align-items: center;
        text-align: center;
        top: 0px;
        width: 100%;
        height: 100%;
        opacity: 1;
        background: transparent;
        color: #fff;
        font-size: 32px;
        font-weight: 600;
    }

    /* .button-submit{
        height: 5.42857rem;
        padding:14px 20px 15px 20px;
        box-sizing:border-box;
    }
    .button-submit button{
        padding:0;
        margin:0;
        width:100%;
        height: 90%;
        border-radius: 3px;
    } */
</style>
