<template>
    <div :class="!kordinatAlamatActive ? 'konten col' : 'konten'">
        <kordinat-alamat :mapConfig="mapConfig" v-if="kordinatAlamatActive"></kordinat-alamat>
        <template v-else>
            <div class="form-group">
                <label>Kabupaten</label>
                <input v-model="kabupaten" type="text" class="form-control" disabled>
            </div>
            <div class="form-group">
                <label>Kecamatan</label>
                <input v-model="kecamatan" type="text" class="form-control" disabled>
            </div>
            <div class="form-group">
                <label>Desa</label>
                <input v-model="desaCom" type="text" placeholder="Tulis Nama Desa" class="form-control">
            </div>
            <div class="d-flex">
                <div class="col pl-0">
                    <div class="form-group">
                        <label>RT</label>
                        <input v-model="rtCom" type="text" placeholder="RT" class="form-control">
                    </div>
                </div>
                <div class="col">
                    <div class="form-group">
                        <label>RW</label>
                        <input v-model="rwCom" type="text" placeholder="RW" class="form-control">
                    </div>
                </div>
                <div class="col pr-0">
                    <div class="form-group">
                        <label>Kode Pos</label>
                        <input v-model="kodePosCom" type="text" placeholder="Kode Pos" class="form-control">
                    </div>
                </div>
            </div>
            <div class="form-group">
                <label>Detail Jalan / Gang</label>
                <textarea v-model="detailsCom" placeholder="Tulis nama jalan, gang & ciri - ciri rumah" class="form-control"></textarea>
            </div>
            <div class="row p-2 mb-3 border-secondary" style="border: 1px dotted">
                <div class="d-flex flex-column">
                    Koordinat Lokasi
                    <small class="text-muted mb-2">Cari posisi rumah Pian di peta</small>
                </div>
                <div class="d-flex align-items-center pl-5">
                    <button @click="setLocation()" class="btn btn-info btn-sm">
                        <template v-if="$store.state.address.kordinat==null">Cari Lokasi</template>
                        <template v-else>Ubah Lokasi</template>
                    </button>
                </div>
            </div>
            <div class="d-flex flex-row-reverse">
                <button v-if="!loading&&!$store.state.address.loading" @click="saveAddress" class="btn btn-lg rounded-0 tombol text-white px-5">Simpan</button>
                <button v-else class="btn btn-lg rounded-0 tombol text-white px-5">
                    <i class="fa fa-spinner fa-spin"></i>
                </button>
            </div>
        </template>
    </div>
</template>
<script>
import KordinatAlamat from '@/components/Map/SetLocationOnMap'
export default {
  components: { KordinatAlamat },
    name:"TambahAlamat",
    data(){
        return {
            loading:false,
            mapConfig:{
                zoom: 15,
                center: {
                    lng: 116.23,
                    lat: -3.23,
                },
                gestureHandling: "greedy",
                mapTypeId: 'hybrid',
                disableDefaultUI: true,
                zoomControl: true,
            },
            kabupaten:'Kotabaru',
            kecamatan:'Pulau Laut Utara',
            desa:this.$store.state.address.desa,
            rt:this.$store.state.address.rt,
            rw:this.$store.state.address.rw,
            kode_pos:this.$store.state.address.kode_pos,
            details:this.$store.state.address.details,  
        }
    },
    computed:{
        desaCom:{
            get(){
                return this.$store.state.address.desa
            },
            set(data){
                this.desa = data;
            }
        },
        rtCom:{
            get(){
                return this.$store.state.address.rt
            },
            set(data){
                this.rt = data;
            }
        },
        rwCom:{
            get(){
                return this.$store.state.address.rw
            },
            set(data){
                this.rw = data;
            }
        },
        kodePosCom:{
            get(){
                return this.$store.state.address.kode_pos
            },
            set(data){
                this.kode_pos = data;
            }
        },
        detailsCom:{
            get(){
                return this.$store.state.address.details
            },
            set(data){
                this.details = data;
            }
        },
        kordinatAlamatActive(){
            const urlParams = new URLSearchParams(window.location.search)
            return JSON.parse(urlParams.get('modals')).includes('kordinat-lokasi')
        }
    },
    watch:{
        desaCom(value){
            this.desa = value;
        },
        rtCom(value){
            this.rt = value;
        },
        rwCom(value){
            this.rw = value;
        },
        kodePosCom(value){
            this.kode_pos = value;
        },
        detailsCom(value){
            this.details = value;
        },
    },
    methods:{
        setLocation(){
            this.axios.get("https://maps.googleapis.com/maps/api/geocode/json?address="+this.details+"%2C"+this.desa.replace(/\s/g, '%2B')+"%2C"+this.kecamatan+"%2C"+this.kabupaten+"%2CKalimantan Selatan&region=ID&key=AIzaSyDFpkPVjM26Py4C4pDe9RiCVWPl4s_pXrw").then(response => {
                var queryParams = new URLSearchParams(window.location.search);
                var q = ['tambah-alamat','kordinat-lokasi'];
                queryParams.set("modals",JSON.stringify(q));
                if(navigator.geolocation){
                    navigator.geolocation.getCurrentPosition(p => {
                        if(p.coords.accuracy<100){
                            this.$store.dispatch('location/save_near_location',{lat:p.coords.latitude,lng:p.coords.longitude});
                        }
                    },() => {},
                    {maximumAge:10000, timeout:5000, enableHighAccuracy: true});
                }
                this.$store.dispatch('location/save_near_location',response.data.results[response.data.results.length-1].geometry.location)
                var payload = {
                    desa:this.desa,
                    rt:this.rt,
                    rw:this.rw,
                    kode_pos:this.kode_pos,
                    details:this.details
                }
                this.$store.dispatch('address/save_address',payload)
                this.$router.push(this.$route.path+'?'+queryParams.toString())
            })
        },

        saveAddress(){
            this.loading = true;
            this.axios.post(this.$store.state.url_api + 'addresses/save',{
                details : this.details,
                rt : this.rt,
                rw : this.rw,
                desa : this.desa,
                kecamatan : this.kecamatan,
                kabupaten : this.kabupaten,
                provinsi : "Kalimantan Selatan",
                kode_pos : this.kode_pos,
                lat : this.$store.state.address.kordinat.lat,
                lng : this.$store.state.address.kordinat.lng,
            }, {
                headers: {
                        "Authorization": "Bearer " + localStorage.token
                }
            }).then(response => {
                if(response.data.meta.statusText == "OK"){
                    this.loading = false;
                    this.$router.back();
                }
            })
        }
    }
}
</script>
<style scoped>
    .konten{
        width: 100%;
        height: calc(100vh - 55px);
        min-height: calc(100vh - 55px);
    }
    .wrap{
        margin:0;
        height:100vh; 
        width:100%;
        background-color:white;
        box-sizing: border-box;
        padding:20px;
    }
</style>