<template>
    <div class="pt-5 mt-2" style="background-color: #F5F5F5">
        <address-content :addresses="addresses"></address-content>
    </div>
</template>
<script>
import AddressContent from './AddressContent.vue'
export default {
  components: { AddressContent },
    name:"Address",
    data(){
        return {
            loading:true
        }
    },
    computed:{
        addresses(){
            return this.$store.state.transaction.addresses
        }
    },
    beforeRouteEnter(to, from, next){
        if(Object.prototype.hasOwnProperty.call(to.query, 'items')){
            if(to.query.items!=null&&to.query.items!=""){
                next()
            }else{
                next({name:'cart'})
            }
        }else{
            next({name:'cart'})
        } 
    },
    created(){
        this.axios.get(this.$store.state.url_api + 'addresses', {
            headers: {
                    "Authorization": "Bearer " + localStorage.token
            }
        }).then(response => {
            this.$store.dispatch('save_addresses', response.data)
            if(response.data.length>0){
                if(this.$store.state.address.desa=="" && this.$store.state.address.rt=="" && this.$store.state.address.rw=="" && this.$store.state.address.kode_pos=="" && this.$store.state.address.details==""){
                    var payload = response.data[0]
                    this.$store.dispatch('address/save_address', {
                            desa:payload.desa,
                            rt:payload.rt,
                            rw:payload.rw,
                            kode_pos:payload.kode_pos,
                            details:payload.jalan,
                    })
                }
                if(this.$store.state.address.kordinat==null){
                    this.$store.dispatch('address/save_kordinat',{
                        lat:response.data[0].lat,
                        lng:response.data[0].lng
                    })
                }
            }
            this.loading = false
        })
        
    }
}
</script>
<style>
    .box{
        margin:0 !important;
        margin-bottom: 10px !important;
        border-top: 1px solid rgb(238, 238, 238);
        border-bottom: 1px solid rgb(238, 238, 238);
        background-color: #fff;
        padding:20px 10px;
    }
    .box-child{
        display: flex;
        justify-content: space-between;
        align-items: center;
    }
    .box-content{
        display: flex;
        flex-direction: column;
        /* padding-right:20px; */
    }
    .text-head{
        font-weight: 700;
        margin-bottom: 5px;
    }
    .text-child{
        font-size: .9em;
        line-height: 1.3em;
    }
</style>